.m-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  font-size: 16.5px;
  z-index: 10000;
}
.m-modal p,
.m-modal span,
.m-modal strong,
.m-modal a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  line-height: 1;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  text-rendering: optimizeLegibility;
}
.m-modal strong {
  font-weight: 700;
}
.m-modal .m-modal-back {
  position: absolute;
  z-index: 10001;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #20232a;
  opacity: 0.8;
}
.m-modal .m-modal-content {
  position: relative;
  z-index: 10002;
  box-sizing: content-box;
  text-align: center;
  min-width: 200px;
  max-width: 240px;
  background-color: #fff;
  opacity: 0.95;
  border-radius: 20px;
  box-shadow: 0 3px 20px rgba(32, 35, 42, 0.5);
  border: 3px solid #333;
  color: #4a4a4a;
  display: flex;
  flex-direction: column;
  overflow: auto;
  padding: 24px;
  transition: 0.5s box-shadow;
}
.m-modal .m-modal-content:hover, .m-modal .m-modal-content:focus, .m-modal .m-modal-content:active {
  opacity: 1;
}
.m-modal .m-modal-content .m-title {
  margin-bottom: 8px;
  overflow: hidden;
  white-space: pre;
  text-overflow: ellipsis;
  line-height: 1.2em;
}
.m-modal .m-modal-content .m-details {
  margin-bottom: 10px;
}
.m-modal .m-modal-content .m-details p {
  white-space: pre;
  font-size: 12px;
  margin-top: 3px;
  margin-bottom: 3px;
}
.m-modal .m-modal-content a {
  cursor: pointer;
  padding: 10px;
  color: #4a4a4a;
  border-radius: 20px;
  text-decoration: none;
}
.m-modal .m-modal-content a.m-gmail {
  color: #c0372a;
}
.m-modal .m-modal-content a.m-gmail:hover, .m-modal .m-modal-content a.m-gmail:focus, .m-modal .m-modal-content a.m-gmail:active {
  background-color: rgba(192, 55, 42, 0.08);
